// --sb-paginate-break-color
// --sb-paginate-disabled-color
// --sb-paginate-dots-color

.sb-paginate {
  text-align: center;

  :focus {
    outline: none;
  }
}

.sb-paginate__container {
  display: inline-block;
  margin: var(--sbt-spacing-0);
  padding: var(--sbt-spacing-40) var(--sbt-spacing-0);

  @include window-width-max-small {
    padding: var(--sbt-spacing-20);
  }
}

%sb-page-style {
  border-radius: var(--sbt-border-radius-circle);
  display: inline-block;
  font-family: var(--sbt-font-stack-primary);
  font-size: var(--sbt-font-size-30);
  line-height: var(--sbt-line-height-ui);
  margin: var(--sbt-spacing-0) var(--sbt-spacing-10);
  padding: var(--sbt-spacing-10) var(--sbt-spacing-20);

  @include window-width-max-small {
    margin: var(--sbt-spacing-0) var(--sbt-spacing-30) var(--sbt-spacing-20)
      var(--sbt-spacing-0);
  }
}

.sb-paginate__previous {
  @extend %sb-page-style;
}

.sb-paginate__active {
  background-color: var(--sb-paginate-active-color, var(--sbt-theme-color-60));
  border: 1px solid var(--sb-paginate-active-color, var(--sbt-theme-color-60));
}

.sb-paginate__disabled {
  color: var(--sbt-theme-tertiary-color-50);
  cursor: not-allowed;
}

.sb-paginate__page {
  @extend %sb-page-style;
}

.sb-paginate__break {
  @extend %sb-page-style;

  border: none;
}

.sb-paginate__next {
  @extend %sb-page-style;
}

%sb-page-link-style {
  color: var(--sb-paginate-link-color, var(--sbt-black));
  cursor: pointer;
  display: inline-block;
  text-align: center;

  &:hover {
    color: var(--sb-paginate-link-hover-color, var(--sbt-black));
    text-decoration: none;
  }
}

%sb-page-link-disabled-style {
  @extend %sb-page-link-style;
}

.sb-paginate__disabled {
  %sb-page-link-disabled-style {
    color: var(
      --sb-paginate-disabled-color,
      var(--sbt-theme-tertiary-color-50)
    );
    cursor: not-allowed;
  }
}

.sb-paginate__previous-link {
  @extend %sb-page-link-disabled-style;
}

.sb-paginate__page-link {
  @extend %sb-page-link-style;
}

.sb-paginate__break-link {
  @extend %sb-page-link-style;

  color: var(--sb-paginate-break-color, var(--sbt-black));
  cursor: default;
  pointer-events: none;
  text-decoration: none;
}

.sb-paginate__next-link {
  @extend %sb-page-link-disabled-style;
}

.sb-paginate__active-link {
  color: var(--sbt-white);

  &:hover {
    color: var(--sbt-white);
  }
}

.sb-paginate__dots {
  .sb-paginate__page {
    background-color: var(
      --sb-paginate-dots-color,
      var(--sbt-theme-tertiary-color-50)
    );
    height: 16px;
    vertical-align: middle;
    width: 16px;
  }

  .sb-paginate__active {
    background-color: var(
      --sb-paginate-active-color,
      var(--sbt-theme-color-60)
    );
    border: none;
    height: 20px;
    vertical-align: middle;
    width: 20px;
  }

  .sb-paginate__active-link,
  .sb-paginate__page-link {
    color: transparent;
  }

  .sb-paginate__previous,
  .sb-paginate__next {
    vertical-align: middle;
  }
}
